.App{
    width: 100%;
    min-height: 100vh;
    background-image: url('../src/assets/sunset-bg.gif');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
}
.brand {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
    padding: 1rem;
}
.brand-name img{
    width: 3rem;
    height: 3rem;
    animation: globe 2s infinite;
}
.brand-name >h1{
    text-shadow: 2px 2px 12px #000000;
}
/* @keyframes globe {
    from{
        transform: rotate3d(1, 0, 0, 360deg);
    }
    
} */
#search{
    width: 35%;
    align-items: center;
    display: flex;
}

#btn-search{
    background: transparent;
    border: none;
    padding: 0;
}
#btn-search button{
    background: transparent;
    border: none;
}
#btn-search img{
    width: 2rem;
    animation: bounce 2s infinite;
    transform: rotate3d(0, 2, 0, 360deg);
}
@keyframes bounce {

    from { transform: translate3d(1, 1, 1);}  
    to { transform: translate3d(0, 2px, 0);}
    
}
.search-location{
    
    background-color: rgba(255, 255, 255, 0.7);
    width: 25vw;
    height: 5vh;
    border-radius: 7px;
    border: none;
    padding: 10px;
    color: #000000;
}
.search-location::placeholder{
    color: grey;
    font-size: 0.9rem;
}
.search-location:focus{
    outline: none;
}
.group{
    display: flex;
    justify-content: space-evenly;
    gap: 0.5rem;
    align-items: center;
    /* border: 2px solid red; */
}
.group h1{
    font-family: 'Nunito', sans-serif;
    text-transform: capitalize;
    color: #fff;
}
.group p{
    /* margin-left: 20rem; */
    font-size: 1.2rem;
}
.group h6{
    margin-top: 3rem;
}
.city {
    margin-top: -1rem;
}
.city{
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}
.degree{
    display: flex;
    justify-content: center;
}
.degree img{
    width: 100px;
    height:100px ;
}
.city, .tem{
    font-size: 3rem;
}
.weather{
    margin-top: 5rem;
    margin-left: 3rem;
}
.box_container{
    display: flex;
    justify-self: center;
    align-items: center;
    gap: 1.2rem;
    animation-duration: 3s;
    animation-name: slide;
    margin: 8% 22%;
}
@keyframes slide {
    from{
        margin-left: 0%;
    }
    to{
        margin-left: 22%;
    }
}
.box{
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(0, 0, 0, 0.75);
    width: 220px;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 5px;
    
}
.box:hover{
    transform: scale(1.1);
}
.box h1,p{
    font-size: 1.2rem;
    font-weight: bold;
}





@media (max-width:730px) {
    .App{
        padding: 2rem 0;
    }
    .brand {
        flex-direction: column;
    }
    .box_container{
        width: 300px;
        flex-direction: column;
    }
    .box, .search-location{   
        width: 50vw;
    }
    
}
